import logo from './logo.svg';
import './App.css';
import React, { lazy, Suspense } from "react";
import {
	BrowserRouter as Router,
	Switch,
	Routes,
	Route,
	Link,
} from "react-router-dom";
import { createBrowserHistory } from "history";
import Timo, { createTimo } from "./context/index";
import 'antd/dist/antd.css';
import './style/global.scss';


import store from "./reduce/redurcer";

import BaseAdmin from "@/components/adminBase/index";


const Test = lazy(() => import('./page/test'));
const Info = lazy(() => import('./page/info'));

const history = createBrowserHistory();

const TimoProvider = createTimo(store.data, store.redurce);

function App() {
	return (
		<div className="App">
			<TimoProvider>
				<Router history={history}>
					<BaseAdmin>
						<Suspense fallback={<div>Loading...12312312313</div>}>
							<Routes>
								<Route path="/a" element={<Test />} />
								<Route path="/info" element={<Info />} />
								<Route path="*" element={<div>404</div>} />
							</Routes>
						</Suspense>
					</BaseAdmin>
				</Router>
			</TimoProvider>
		</div>
	);
}

export default App;
